html, body {
    height: 100%;
    width: 100%;
    margin:0;
    padding:0;
}
body {
  font-family: Arial, Helvetica, sans-serif;
  font-size:16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  line-height: 1.5em;
  color: #ccc;
  text-align: center;
  background: #151515 url('/static/images/bg_body.jpg') no-repeat center top;
}
img {
  border:none;
}
article, aside, details, figcaption, figure, footer, header, hgroup, main, nav, section, summary {
  display: block;
}
::-webkit-scrollbar {
  width:2px;
}
#root {
  margin:0 auto;
}
@media screen and (min-width:1200px) {
#root {
  width:1200px;
}
}
.carousel-root.banner {
  border-top: 5px solid #366fa5;
  border-bottom: 2px solid #366fa5;
}
.carousel .control-dots {
  position: absolute;
  top: 0;
  margin: 1em;
  padding: 0 2em 0 0 !important;
  width: 100%;
  text-align:right !important;
}
#yt-carousel-pane .carousel .control-dots {
  position: absolute;
  top:auto;
  bottom: -8px;
  padding: 0 2em 0 0 !important;
  width: 100%;
  text-align:right !important;
}
.yt-box-out {
  position: fixed;
  z-index:1200;
  width:70%;
  top:50%;
  border:3px solid #333;
  left:50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.tiktok-box-out {
  position: fixed;
  z-index:1200;
  width:70%;
  top:50%;
  left:50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.yt-box-in {
  width:100%;
  height:0;
  padding-bottom:56.25%;
  z-index:1100;
}
.yt-box-close {
  position: absolute;
  top: -15px;
  right: -15px;
  width: 30px;
  height: 30px;
  cursor: pointer;
  background:url('/static/images/box-close.png');
  z-index: 1300;
}
.yt-video {
  position:absolute;
  left:0;
  top:0;
  width:100%;
  height:100%;
}
.overlay {
  position:fixed;
  left:0;
  top:0;
  right:0;
  bottom:0;
  z-index:1000;
  background:rgba(0.5,0.5,0.5,0.8);
}




#depositcurrency {
  border: none;
  color:#999;
  border-radius:5px;
  -webkit-box-shadow: inset 0px 2px 2px rgba(0,0,0,0.5);
  -moz-box-shadow: inset 0px 2px 2px rgba(0,0,0,0.5);
  text-shadow: 1px 1px 0px #000;
  box-shadow: inset 0px 2px 2px rgba(0,0,0,0.5);
  background-color: #3e3e3e;
  background: -webkit-gradient(linear, left top, left bottom, from(#545454), to(#3e3e3e));
  background: -moz-linear-gradient(top, #545454, #3e3e3e);
}
#depositcurrency option {
  color:#999;
  background-color: #3e3e3e;
}
#pppane {
  display:none;
}
